import React from 'react'

import { Modal, Button } from 'react-bootstrap';
import { IoTrash, } from 'react-icons/io5'

const DeletePopup = ({ message, yes, no, onClickDeleteConfirm, onClickDeleteCancel }) => {
  return (
    <Modal show='true' centered>
        <Modal.Header className="d-flex justify-content-center">
          <Modal.Title><IoTrash /> { message }</Modal.Title>
        </Modal.Header>
        <Modal.Footer className="d-flex justify-content-center">
          <Button variant="danger" onClick={ onClickDeleteConfirm }>{ yes }</Button>
          <Button variant="secondary" onClick={ onClickDeleteCancel }>{ no }</Button>
        </Modal.Footer>
      </Modal>
  );
}

export default DeletePopup