import React, { useState } from 'react'
import { Tooltip } from 'react-tooltip';
import { IoHome, IoCall, IoSearchOutline } from 'react-icons/io5'

const InvoiceFormStep1 = ({ customers, selectedCustomer, selectCustomer }) => {

    const [search, setSearch] = useState('');

    const searchCustomer = (e) => {
        selectCustomer(null);
        setSearch(e.target.value);
    }

    const filteredCustomerList = search ? customers.filter((item) =>
        item.name.toLowerCase().includes(search.toLowerCase())
    ) : customers;

    return (
    <>
        <div className="input-group">
            <input type="text" className="form-control" placeholder="Search"
            value={ search }
            onChange={ searchCustomer }/>
            <button className="btn btn-primary btn-sm"><IoSearchOutline /></button>
        </div>
        <br />
        <table className="table table-striped">
            <tbody>
            {filteredCustomerList.map((i) => (
                <tr key={i.id} 
                    style={{ cursor: 'pointer',  }}
                    onClick={ () => selectCustomer(i) }
                >
                    <td className={
                        (selectedCustomer !== null && selectedCustomer.id === i.id) ? 'bg-dark text-white' :
                        'bg-light'
                    }
                    data-tooltip-id={`invoice-tooltip-${i.id}`}>{ i.name }
                    <Tooltip id={`invoice-tooltip-${i.id}`} place="right" align="bottom" 
                        className="tooltip-content">
                            <span>
                                <table>
                                    <tbody>
                                        <tr>
                                            <td><IoCall /> </td>
                                            <td>{ i.mobileOne }, { i.mobileTwo }</td>
                                        </tr>
                                        <tr>
                                            <td><IoHome /></td>
                                            <td> { i.addressLine1 }, { i.addressLine2 },</td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td> { i.city }, { i.state } - { i.pinCode }</td>
                                        </tr>
                                        <tr>
                                            <td><b>GST&nbsp;</b></td>
                                            <td> { i.gstNumber }</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </span>  
                        </Tooltip>
                    </td>   
                </tr>
            ))}
            </tbody>
        </table>
    </>
    )
}

export default InvoiceFormStep1