import { CustomerModel } from "../model/CustomerModel";
import { CUSTOMER_URL } from "./Api"

export const getAllCustomers = async () => {
    try {
        const response = await fetch(CUSTOMER_URL + "getAll", {
            method: 'GET',
            headers: {
            'Content-Type': 'application/json',
            },
        }); 
        if(response.ok) {
            const body = await response.json();
            const customerList = body.data
                .map(i => {
                    return new CustomerModel(i.id,i.name,i.gstNumber,i.mobileOne,i.mobileTwo,i.email,i.addressLine1, i.addressLine2, i.city, i.state, i.pinCode);
                })
                .sort((a, b) => a.id - b.id);
            return customerList;
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::CustomerService.getAllCustomers()");
        throw err;
    } 
}

export const createCustomer = async (customer) => {
    try {
        const response = await fetch(CUSTOMER_URL + "create", {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            },
            body: JSON.stringify(customer),
        }); 
        if(response.ok) {
            const body = await response.json();
            return body;
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::CustomerService.createCustomer()");
        throw err;
    } 
}

