import React, {  useState } from 'react'
import "./Customer.css"
import { deleteCustomer } from '../../service/CustomerService'
import Spinner from '../spinner/Spinner'
import CustomerForm from "./form/CustomerForm"
import CustomerList from "./list/CustomerList"
import { useCustomers } from '../../service/CustomerContext'
import DeletePopup from '../popup/DeletePopup'

const Customer = () => {

  const { customers, getCustomersList, customerLoading } = useCustomers();
  const [isCreate, setIsCreate] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [editCustomer, setEditCustomer] = useState(null);
  const [isDelete, setIsDelete] = useState(false);
  const [deleteId, setDeleteId] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const onClickCreate = () => {
    setIsCreate(true);
    setIsEdit(false);
  }

  const onEdit = (customerToBeEdited) => {
    console.log("Customer.onEdit()::Customer::" + JSON.stringify(customerToBeEdited));
    setIsEdit(true);
    setEditCustomer(customerToBeEdited);
  }

  const onDelete = (id) => {
    console.log("Customer.onDelete()::ID::" + id);
    setIsDelete(true);
    setDeleteId(id);
  }

  const submitForm = () => {
    closeForm();
    getCustomersList();
  }

  const closeForm = () => {
    setIsCreate(false);
    setIsEdit(false);
  }

  const onClickDeleteConfirm = () => {
    setIsLoading(true);
    deleteCustomer(deleteId)
      .then((data) => {
        console.log(data);
      }).catch((err) => {
        console.log(err);
      }).finally(() => {
        setIsLoading(false);
        onClickDeleteCancel();
        getCustomersList();
      });
  }

  const onClickDeleteCancel = () => {
    setIsDelete(false);
    setDeleteId(0);
  }

  return (
    <>
      { (customerLoading || isLoading) && <Spinner/> } 

      <div className="scrollable-div" >

        { !customerLoading && 
          <CustomerList 
          customers = { customers } 
          onEdit = { onEdit }
          onDelete = { onDelete }
          onClickCreate = { onClickCreate }
          /> 
        }
        { (isCreate || isEdit) && 
          <CustomerForm 
          submitForm = { submitForm }
          closeForm = { closeForm }
          isEdit = { isEdit }
          editCustomer = { editCustomer }
          /> 
        }
        { isDelete &&
          <DeletePopup 
          message = {'Are you sure you want to Delete?'}
          yes = {'Delete'}
          no = {'Cancel'}
          onClickDeleteConfirm={ onClickDeleteConfirm }
          onClickDeleteCancel={ onClickDeleteCancel }
          />
        }

      </div>
    
    </>
  )
}

export default Customer