import React, { useState, useEffect } from "react";
import "./Home.css";
import { IoFolderOpenOutline, IoPieChartOutline, IoPeopleOutline, IoPowerOutline } from 'react-icons/io5';
import { Link, Outlet, useLocation, useNavigate } from "react-router-dom";
import DeletePopup from "../popup/DeletePopup";

const Home = ({ isAdmin }) => {
  // Track the active state of each link
  const [activeLink, setActiveLink] = useState("");
  
  // Use the useLocation hook to get the current route
  const location = useLocation();

  // Set the active link based on the current route
  useEffect(() => {
    setActiveLink(location.pathname); // Set the active link based on the current path
  }, [location]); // Dependency on location ensures it updates when the route changes

  const [isLogout, setIsLogout] = useState(false);
  const navigate = useNavigate();

  const onClickLogoutConfirm = () => {
    sessionStorage.removeItem("token");
    sessionStorage.removeItem("role");
    setIsLogout(false);
    navigate("/login");
  }

  return (
    <>
      <div className="home-left">
        <ul>
          <li><a></a></li>
          <li>
            <Link to="/home/customers" className={activeLink === "/home/customers" ? "active" : ""} >
              <IoPeopleOutline />
              <span>Customers</span>
            </Link>
          </li>
          <li>
            <Link to="/home/invoices" className={activeLink === "/home/invoices" ? "active" : ""} >
              <IoFolderOpenOutline />
              <span>Invoices</span>
            </Link>
          </li>
          { isAdmin &&
            <li>
              <Link to="/home/statistics" className={activeLink === "/home/statistics" ? "active" : ""} >
                <IoPieChartOutline />
                <span>Statistics</span>
              </Link>
            </li>
          }
          <li><a></a></li>
          <li onClick={ e => setIsLogout(true) }>
            <Link>
              <IoPowerOutline />
              <span>Logout</span>
            </Link>
          </li>
        </ul>
      </div>
      <div className="home-right">
        <Outlet />
      </div>

      {
        isLogout &&
        <DeletePopup 
          message = {'Are you sure you want to Logout?'}
          yes = {'Logout'}
          no = {'Cancel'}
          onClickDeleteConfirm={ onClickLogoutConfirm }
          onClickDeleteCancel={ e => setIsLogout(false) }
        />
      }
    </>
  );
};

export default Home;
