import { useEffect, useState } from 'react';
import "./Login.css"
import { loginUser } from '../../service/LoginService';
import { useNavigate } from 'react-router-dom';
import Spinner from '../spinner/Spinner'
import { useCustomers } from '../../service/CustomerContext';
import lamdapay_logo from '../../assets/lambdapay_logo.png';

const Login = ({ setIsAuthenticated, setIsAdmin }) => {

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [isLoading, setisLoading] = useState(false);
    const navigate = useNavigate();
    const { customerLoading } = useCustomers();
    const [loginSuccess, setLoginSuccess] = useState(false);
  
    const handleSubmit = async (e) => {
      e.preventDefault();
      setisLoading(true);
      setErrorMessage('');
      // setTimeout(() => {
      //   setisLoading(false)
      //   navigate("/home");
      // }, 5000);
      sessionStorage.removeItem("token");
      sessionStorage.removeItem("role");

      loginUser(username, password) 
        .then((result) => {
          console.log(result);
          sessionStorage.setItem("token", result.data);
          sessionStorage.setItem("role", result.status);
          setIsAuthenticated(true);
          setIsAdmin(result.status !== null && result.status === 'ADMIN');
          setLoginSuccess(true); // wait for customer loading to finish
        }).catch((err) => {
          console.log(err);
          setErrorMessage('Invalid Username/Password');
        }).finally(() => {
          setisLoading(false);
        });
    };
    
    // 🔁 Redirect only when login succeeds AND customer loading is done
    useEffect(() => {
      if (loginSuccess && !customerLoading) {
        navigate("/home/invoices");
      }
    }, [loginSuccess, customerLoading, navigate]);

  
    return (
      <>
        {(isLoading || (loginSuccess && customerLoading)) && <Spinner />}
        <div className="login-container">
          <div className="top"></div>
          <div className="bottom"></div>

          <div className="center">
            <h2 className="header"> 
              <img className="logo" src={ lamdapay_logo } alt="Logo" />Lambdapay 
            </h2> 

            <form onSubmit={handleSubmit}>
              <input type="text" 
                placeholder="Username"
                value={username} 
                onChange={(e) => setUsername(e.target.value)}
                required 
                tabIndex="1"/>
              <input type="password" 
                placeholder="Password" 
                value={password} 
                onChange={(e) => setPassword(e.target.value)}
                required 
                tabIndex="2"/>
              <button type="submit" tabIndex="3">Sign In</button>
            </form>

            {errorMessage && <p style={{color:'red'}}>{errorMessage}</p>}
            <h2>&nbsp;</h2>
          </div>
          
        </div>
      </>
    );
  
}

export default Login