import React, { useState } from 'react'
import InvoiceList from './list/InvoiceList'
import { deleteInvoice, downloadExport, getInvoicesByFilter, updatePayment } from '../../service/InvoiceService'
import InvoiceForm from './form/InvoiceForm'

import "./Invoice.css";
import DeletePopup from '../popup/DeletePopup'
import Spinner from '../spinner/Spinner'
import PaymentForm from './payment/PaymentForm'
import SearchBar from './search/SearchBar'
import { useCustomers } from '../../service/CustomerContext';

const Invoice = () => {

  const { customers, customerLoading } = useCustomers();

  const defaultFilter = {
    type : 'NA',
    paymentStatus : 'NA',
    customerId : 0,
    fromDate : '',
    toDate : ''
  };

  const [invoices, setInvoices] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isCreate, setIsCreate] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [editInvoice, setEditInvoice] = useState(null);
  const [editInvoiceCustomer, setEditInvoiceCustomer] = useState(null);
  const [isDelete, setIsDelete] = useState(false);
  const [deleteId, setDeleteId] = useState(0);
  const [isUpdate, setIsUpdate] = useState(false);
  const [filter, setFilter] = useState(defaultFilter);
  const [headingMessage, setHeadingMessage] = useState("Showing 0 invoices");
  const [customerName, setCustomerName] = useState('All Customers');
  const [validFilterForExport, setValidFilterForExport] = useState(false);

  const onClickCreate = () => {
    setIsCreate(true);
    setIsEdit(false);
  }

  const onClickEdit = (invoice, customer) => {
    // console.log("Invoice.onClickEdit()::Invoice::" + JSON.stringify(invoice) + ",Customer::" + JSON.stringify(customer))
    setIsEdit(true);
    setEditInvoice(invoice);
    setEditInvoiceCustomer(customer);
  }

  const onClickDelete = (id) => {
    console.log("Invoice.onClickDelete().ID::" + id);
    setIsDelete(true);
    setDeleteId(id);
  }

  const closeForm = () => {
    setIsCreate(false);
    setIsEdit(false);
  }

  const submitForm = () => {
    closeForm();
    onClickSearch(filter);
  }

  const onClickDeleteConfirm = () => {
    setIsDelete(false);
    setIsLoading(true);
    deleteInvoice(deleteId)
      .then((data) => {
        console.log(data);
      }).catch((err) => {
        console.log(err);
      }).finally(() => {
        setIsLoading(false); 
        setDeleteId(0);   
        onClickSearch(filter);
      });
    }
  
    const onClickDeleteCancel = () => {
      setIsDelete(false);
      setDeleteId(0);
    }

    const onClickUpdate = (invoice) => {
      setIsUpdate(true);
      setEditInvoice(invoice);
    }

    const onClickUpdateConfirm = (updatedPaymentObject, id, token) => {
      closePaymentForm();
      setIsLoading(true);
      console.log("Invoice.onClickUdpateConfirm()::" + JSON.stringify(updatedPaymentObject));
      updatePayment(updatedPaymentObject, id, token)
        .then((data) => {
          console.log(data);
        }).catch((err) => {
          console.log(err);
        }).finally(() => {
          closePaymentForm();
          setIsLoading(false);
        });
    }

    const onClickUpdateCancel = () => {
      setIsUpdate(false);
      setEditInvoice(null);
    }

    const onClickSearch = (filter) => {
      if(filter.fromDate === '1970-01-01T00:00:00.000Z' || filter.fromDate === '' 
        || filter.toDate === '1970-01-01T00:00:00.000Z' || filter.toDate === '') {
          console.log("Invoice.onClickSearch()::Invalid Filter");
      } else {
        setIsLoading(true);
        getInvoicesByFilter(filter)
          .then((i) => {
            console.log("Invoice.onClickSearch().List size::" + i.length);
            setInvoices(i);
            setHeadingMessage(getHeadingMessage(i.length, customerName));
          })
          .catch((err) => {
            console.log(err);
            setInvoices([]);
            setHeadingMessage(getHeadingMessage(0, 'All Customers'));
          })
          .finally(() => {
            setIsLoading(false); 
          });
      }
    }

    const onClickResetSearch = () => {
      setInvoices([]);
      setFilter(defaultFilter);
      setCustomerName('All Customers');
      setHeadingMessage(getHeadingMessage(0, 'All Customers'));
    }

    const closePaymentForm = () => {
      setIsUpdate(false);
      setEditInvoice(null);
      onClickSearch(filter);
    }

    const getHeadingMessage = (size, name) => {
      return (
        <span>Showing <b>{ size }</b> invoices of <b>{ name }</b></span>
      ); 
    }

    const onClickExport = () => {
      const customerObj = customers.find(i => i.id == filter.customerId);
      setIsLoading(true);
      downloadExport(filter, customerObj)
        .then((data) => {
          console.log("Invoice.onClickExport()::Data received for Filter::" + JSON.stringify(filter));
        }).catch((err) => {
          console.log(err);
        }).finally(() => {
          setIsLoading(false);
        });
    }

  return (

    <div className="scrollable-div" >

      { isLoading && <Spinner/> }   

      {
        customerLoading ? (
          <Spinner/>
        ) : (
          <>
            <SearchBar 
              onClickSearch = { onClickSearch }
              onClickResetSearch = { onClickResetSearch }
              filter = { filter }
              setFilter = { setFilter }
              setCustomerName = { setCustomerName }
              setValidFilterForExport = { setValidFilterForExport }
            />
            { !isLoading && 
              <InvoiceList 
              invoices = { invoices }
              customers = { customers }
              loading = { customerLoading }
              headingMessage = { headingMessage }
              onClickCreate = { onClickCreate }
              onClickEdit = { onClickEdit }
              onClickDelete = { onClickDelete }
              onClickUpdate = { onClickUpdate }
              onClickExport = { onClickExport }
              validFilterForExport = { validFilterForExport }
              />
            } 
            {(isCreate || isEdit) &&
              <InvoiceForm 
                submitForm = { submitForm }
                closeForm = { closeForm }
                isEdit = { isEdit }
                editInvoice = { editInvoice }
                editInvoiceCustomer = { editInvoiceCustomer }
              />
            }
            { isDelete &&
              <DeletePopup 
              message = {'Are you sure you want to Delete?'}
              yes = {'Delete'}
              no = {'Cancel'}
              onClickDeleteConfirm={ onClickDeleteConfirm }
              onClickDeleteCancel={ onClickDeleteCancel }
              />
            }
            {
              isUpdate &&
              <PaymentForm
              invoice = { editInvoice }
              closePaymentForm = { closePaymentForm }
              onClickUpdateConfirm = { onClickUpdateConfirm }
              onClickUpdateCancel = { onClickUpdateCancel }
              />
            }
          </>
        )
      }
  
    </div>
  
  )
}

export default Invoice