import { BASE_URL } from "./Api"

export const loginUser = async (username, password) => {
    const user = { userName: username, password: password, userRole: "ROLE"};
    try {
        const response = await fetch(BASE_URL + 'user/validate', {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            },
            body: JSON.stringify(user),
        }); 
        if(response.ok) {
            const body = await response.json();
            return body;
        } else {
            const errorBody = await response.json(); // Read error body
            throw errorBody; // Directly throw the error body and stringify it to catch it in component.
        }
    } catch(err) {
        console.log("Error::LoginService.loginUser()");
        throw err;
    } 
}