import { HashRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import React, { useState, useEffect } from "react";
import './App.css';
import Login from './components/login/Login';
import Home from './components/home/Home';
import Customer from './components/customer/Customer';
import Invoice from './components/invoice/Invoice';
import Statistics from './components/statistics/Statistics';
import { CustomerProvider } from './service/CustomerContext';
import AuthGuard from './components/guard/AuthGuard';

function App() {

  const [isAuthenticated, setIsAuthenticated] = useState(
    sessionStorage.getItem("token") !== null
  );

  const [isAdmin, setIsAdmin] = useState(
    sessionStorage.getItem("role") !== null && 
    sessionStorage.getItem("role") === "ADMIN"
  );

  useEffect(() => {
    const handleStorageChange = () => {
      setIsAuthenticated(sessionStorage.getItem("token") !== null);
      setIsAdmin(sessionStorage.getItem("role") !== null && sessionStorage.getItem("role") === "ADMIN");
    };

    // In case token changes in same tab
    window.addEventListener('storage', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []);

  return ( 
    <CustomerProvider>
      <div className="app">
        <Router>
          <Routes>
            <Route path="/" element={ <Navigate to ="/login" /> } />
            <Route path="/login" element={ 
              <Login 
                setIsAuthenticated={ setIsAuthenticated } 
                setIsAdmin={ setIsAdmin }
              /> 
              } />

            <Route element={<AuthGuard isAuthenticated={ isAuthenticated } />}>
              <Route path="/home" element={ <Home isAdmin={ isAdmin }/> }>
                <Route path="customers" element={ <Customer /> } />
                <Route path="invoices" element={ <Invoice /> } />
                {
                  isAdmin && 
                  <Route path="statistics" element={<Statistics />} />
                }
              </Route>
            </Route>
            
            <Route path="*" element={ <Navigate to ="/login" /> } />
          </Routes>
        </Router>
      </div>
    </CustomerProvider>
  );
}

export default App;
