import React, { useState } from 'react'
import "./InvoiceForm.css"
import { Modal, Button } from 'react-bootstrap'; 
import { useCustomers } from '../../../service/CustomerContext';
import InvoiceFormStep1 from './InvoiceFormStep1';
import InvoiceFormStep2 from './InvoiceFormStep2';
import Spinner from '../../spinner/Spinner';
import { createGSTInvoice, createLabourInvoice, updateGSTInvoice, updateLabourInvoice } from '../../../service/InvoiceService';

const InvoiceForm = ({ submitForm, closeForm, isEdit, editInvoice, editInvoiceCustomer }) => {

  const { customers } = useCustomers();

  const summaryObject = {
    description : '',
    dcNumber : '',
    hsnCode : '',
    rate : '0.00',
    quantity : '0.00',
    total : '0.00'
  }

  const taxObject = {
    cgstPercentage : '9.00',
    sgstPercentage : '9.00',
    igstPercentage : '0.00',
    cgstAmount : '0.00',
    sgstAmount : '0.00',
    igstAmount : '0.00'
  }

  const paymentObject = {
    paidAmount : '0.00',
    outstandingAmount : '0.00',
    paymentStatus : 'NOT_PAID',
    paymentType : 'NA',
    paymentDate : '',
    vehicleNumber : '',
    referenceNumber : ''
  }
  
  const [selectedCustomer, setSelectedCustomer] = useState(isEdit ? editInvoiceCustomer : null);
  const [pageNumber, setPageNumber] = useState(isEdit ? 2 : 1); // State to control page number
  const [isLoading, setIsLoading] = useState(false);

  const [type, setType] = useState(isEdit ? editInvoice.type : 'GST');
  const [quote, setQuote] = useState(isEdit ? editInvoice.quote : false);

  const [summary1, setSummary1] = useState(isEdit ? editInvoice.summaryList[0] : summaryObject);
  const [summary2, setSummary2] = useState(isEdit ? editInvoice.summaryList[1] : summaryObject);
  const [summary3, setSummary3] = useState(isEdit ? editInvoice.summaryList[2] : summaryObject);
  const [summary4, setSummary4] = useState(isEdit ? editInvoice.summaryList[3] : summaryObject);
  const [summary5, setSummary5] = useState(isEdit ? editInvoice.summaryList[4] : summaryObject);

  const [totalAmountBeforeTax, setTotalAmountBeforeTax] = useState(isEdit ? editInvoice.totalAmountBeforeTax : '0.00');
  const [tax, setTax] = useState(isEdit ? editInvoice.tax : taxObject);
  const [totalAmount, setTotalAmount] = useState(isEdit ? editInvoice.totalAmount : '0.00');

  const [payment, setPayment] = useState(isEdit ? 
    {
      paidAmount : editInvoice.paidAmount,
      outstandingAmount : editInvoice.outstandingAmount,
      paymentStatus : editInvoice.paymentStatus,
      paymentType : editInvoice.paymentType,
      paymentDate : editInvoice.paymentDate,
      vehicleNumber : editInvoice.vehicleNumber,
      referenceNumber : editInvoice.referenceNumber
    }
    : paymentObject);

  const handlePrev = () => {
    setSelectedCustomer(null);
    if(pageNumber === 1) { 
      closeForm();
    } else if (pageNumber === 2) {
      setPageNumber(1);
    }
  }

  const handleNext = () => {
    if(pageNumber === 1) {
      setPageNumber(2);
    } else if (pageNumber === 2) {
      const invoicePayload = {
        id : isEdit ? editInvoice.id : 0,
        customerId : (selectedCustomer.id === null) ? 0 : selectedCustomer.id,
        type : type,
        quote : quote,
        summaryList : [ 
          {...summary1, quantity : roundOff(summary1.quantity), rate : roundOff(summary1.rate)}, 
          {...summary2, quantity : roundOff(summary2.quantity), rate : roundOff(summary2.rate)}, 
          {...summary3, quantity : roundOff(summary3.quantity), rate : roundOff(summary3.rate)}, 
          {...summary4, quantity : roundOff(summary4.quantity), rate : roundOff(summary4.rate)}, 
          {...summary5, quantity : roundOff(summary5.quantity), rate : roundOff(summary5.rate)} 
        ],
        tax : {...tax,
          cgstPercentage : roundOff(tax.cgstPercentage),
          sgstPercentage : roundOff(tax.sgstPercentage),
          igstPercentage : roundOff(tax.igstPercentage),
        },
        totalAmountBeforeTax : totalAmountBeforeTax,
        totalAmount : roundOffTotal(totalAmount),
        paidAmount : roundOff(payment.paidAmount),
        outstandingAmount : roundOff(payment.outstandingAmount),
        paymentStatus : payment.paymentStatus,
        paymentType : payment.paymentType,
        paymentDate : payment.paymentDate,
        referenceNumber : payment.referenceNumber,
        vehicleNumber : payment.vehicleNumber,
        tdsAmount : isEdit ? editInvoice.tdsAmount : '0.00',
        active : true,
        createdBy : isEdit ? editInvoice.createdBy : sessionStorage.getItem("token"),
        createdDate : isEdit ? editInvoice.createdDate : '',
        modifiedBy : isEdit ? editInvoice.modifiedBy : sessionStorage.getItem("token"),
        modifiedDate : isEdit ? editInvoice.createdDate : ''
      };

      // console.log("InvoiceForm.handleNext()::" + JSON.stringify(invoicePayload));

      setIsLoading(true);
      if(!isEdit) {
        if(invoicePayload.type === 'GST') {
          createGSTInvoice(selectedCustomer, invoicePayload)
          .then((data) => {
            console.log("InvoiceForm.handleNext().createGSTInvoice()::Invoice created");
          }).catch((err) => {
            console.log(err);
          }).finally(() => {
            setIsLoading(false);
            submitForm();
          });
        } else if(invoicePayload.type === 'LABOUR') {
            createLabourInvoice(selectedCustomer, invoicePayload)
            .then((data) => {
              console.log("InvoiceForm.handleNext().createLabourInvoice()::Invoice updated");
            }).catch((err) => {
              console.log(err);
            }).finally(() => {
              setIsLoading(false);
              submitForm();
            });
        }
      } else if(isEdit) {
        if(invoicePayload.type === 'GST') {
          updateGSTInvoice(selectedCustomer, invoicePayload)
          .then((data) => {
            console.log("InvoiceForm.handleNext().updateGSTInvoice()::Invoice created");
          }).catch((err) => {
            console.log(err);
          }).finally(() => {
            setIsLoading(false);
            submitForm();
          });
        } else if(invoicePayload.type === 'LABOUR') {
          updateLabourInvoice(selectedCustomer, invoicePayload)
          .then((data) => {
            console.log("InvoiceForm.handleNext().updateLabourInvoice()::Invoice updated");
          }).catch((err) => {
            console.log(err);
          }).finally(() => {
            setIsLoading(false);
            submitForm();
          });
        }
      }
    }
  }

  const selectCustomer = (customer) => {
    setSelectedCustomer(customer);
  }

  const roundOff = (value) => {
    return (parseFloat(value) || 0).toFixed(2);
  }

  const roundOffTotal = (value) => {
    const num = parseFloat(value); 
    if (isNaN(num)) {
      return "0.00";
    }
    const rounded = Math.round(num);
    return rounded.toFixed(2); // returns a string
  }


  return (
    
    <div>
      { isLoading && <Spinner />  } 
      { !isLoading &&
        <Modal show='true' onHide={ ()=> closeForm() } dialogClassName="custom-modal-width" centered>
          <Modal.Header closeButton>
              <Modal.Title>
                  { pageNumber === 1 && `Step 1/2 : Choose Customer` }
                  { pageNumber === 2 && 
                    isEdit ? ("Edit Invoice = #" + editInvoice.id) : (`Step 2/2 : Create Invoice`) 
                  }
                  
              </Modal.Title>
          </Modal.Header>
          <Modal.Body style={{ maxHeight: '65vh',  height:'65vh', overflowY: 'auto' }}>
              { 
                  pageNumber === 1 &&
                  <InvoiceFormStep1 
                      customers = { customers }
                      selectedCustomer = { selectedCustomer }
                      selectCustomer = { selectCustomer }
                    />
              }
              {
                  pageNumber === 2 &&
                  <InvoiceFormStep2
                      isEdit = { isEdit }
                      selectedCustomer = { selectedCustomer }
                      type = { type }
                      setType = { setType }
                      quote = { quote }
                      setQuote = { setQuote }
                      summary1 = { summary1 }
                      summary2 = { summary2 }
                      summary3 = { summary3 }
                      summary4 = { summary4 }
                      summary5 = { summary5 }
                      setSummary1 = { setSummary1 }
                      setSummary2 = { setSummary2 }
                      setSummary3 = { setSummary3 }
                      setSummary4 = { setSummary4 }
                      setSummary5 = { setSummary5 }
                      totalAmountBeforeTax = { totalAmountBeforeTax }
                      setTotalAmountBeforeTax = { setTotalAmountBeforeTax }
                      tax = { tax }
                      setTax = { setTax }
                      totalAmount = { totalAmount }
                      setTotalAmount = { setTotalAmount }
                      payment = { payment }
                      setPayment = { setPayment }
                  />
              }
          </Modal.Body>
          <Modal.Footer>
              <Button variant="danger" onClick={ handlePrev } disabled={ isEdit } >
                {pageNumber === 1 && `Cancel`}
                {pageNumber === 2 && `Previous`}
              </Button>
              <Button variant="primary" onClick={ handleNext } disabled={ selectedCustomer === null } >
                {pageNumber === 1 && `Next`}
                {pageNumber === 2 && `Save & Print`}
              </Button>
          </Modal.Footer>
        </Modal>
      }
      
    </div>
  )
}

export default InvoiceForm