import React from 'react'
import { IoHome, IoCall } from 'react-icons/io5'

const InvoiceFormStep2 = ({ 
    isEdit, selectedCustomer,
    type, setType,
    quote, setQuote, 
    summary1, summary2, summary3, summary4, summary5, setSummary1, setSummary2, setSummary3, setSummary4, setSummary5,
    totalAmountBeforeTax, setTotalAmountBeforeTax, 
    tax, setTax, 
    totalAmount, setTotalAmount, 
    payment, setPayment
}) => {

    // Handle button click to toggle between buttons
    const handleButtonClick = () => {
        if(type === 'GST') {
            setType('LABOUR'); // Toggle the state
            setTax({
                cgstPercentage : '0.00',
                sgstPercentage : '0.00',
                igstPercentage : '0.00',
                cgstAmount : '0.00',
                sgstAmount : '0.00',
                igstAmount : '0.00'
            });
            setTotalAmount(totalAmountBeforeTax);
        } else {
            setType('GST');
            const updatedCgstAmount = ((float('9.00') / 100) * float(totalAmountBeforeTax)).toFixed(2);
            const updatedSgstAmount = ((float('9.00') / 100) * float(totalAmountBeforeTax)).toFixed(2);
            const updatedIgstAmount = ((float('0.00') / 100) * float(totalAmountBeforeTax)).toFixed(2);
            setTax({...tax,
                cgstPercentage : '9.00',
                sgstPercentage : '9.00',
                igstPercentage : '0.00',
                cgstAmount : updatedCgstAmount,
                sgstAmount : updatedSgstAmount,
                igstAmount : updatedIgstAmount,
            });
            setTotalAmount((float(totalAmountBeforeTax) + float(updatedCgstAmount) + float(updatedSgstAmount)
                + float(updatedIgstAmount)).toFixed(2));
        }
    };
    
    const onSummaryChange = (value, row, index) => {
        switch(index) {
            case 1 : 
                switch (row) {
                    case 1 : setSummary1({...summary1, description : value}); break;
                    case 2 : setSummary2({...summary2, description : value}); break;
                    case 3 : setSummary3({...summary3, description : value}); break;
                    case 4 : setSummary4({...summary4, description : value}); break;
                    case 5 : setSummary5({...summary5, description : value}); break;
                    default : break;
                }
                break;
            case 2 : 
                switch (row) {
                    case 1 : setSummary1({...summary1, dcNumber : value}); break;
                    case 2 : setSummary2({...summary2, dcNumber : value}); break;
                    case 3 : setSummary3({...summary3, dcNumber : value}); break;
                    case 4 : setSummary4({...summary4, dcNumber : value}); break;
                    case 5 : setSummary5({...summary5, dcNumber : value}); break;
                    default : break;
                }
                break;
            case 3 : 
                switch (row) {
                    case 1 : setSummary1({...summary1, hsnCode : value}); break;
                    case 2 : setSummary2({...summary2, hsnCode : value}); break;
                    case 3 : setSummary3({...summary3, hsnCode : value}); break;
                    case 4 : setSummary4({...summary4, hsnCode : value}); break;
                    case 5 : setSummary5({...summary5, hsnCode : value}); break;
                    default : break;
                }
                break;
            case 4 : 
                const updatedQty = validateDecimalInput(value);
                let updatedTotal;
                let updatedTotalBeforeTax;
                switch (row) {
                    case 1 : 
                        updatedTotal = (float(updatedQty) * float(summary1.rate)).toFixed(2);
                        updatedTotalBeforeTax = (float(updatedTotal) + float(summary2.total) + float(summary3.total)
                            + float(summary4.total) + float(summary5.total));
                        setSummary1({...summary1, 
                            quantity : updatedQty,
                            total : updatedTotal
                        }); 
                        updateTotalAndTax(updatedTotalBeforeTax);
                        break;
                    case 2 : 
                        updatedTotal = (float(updatedQty) * float(summary2.rate)).toFixed(2);
                        updatedTotalBeforeTax = (float(summary1.total) + float(updatedTotal) + float(summary3.total) 
                            + float(summary4.total) + float(summary5.total));
                        setSummary2({...summary2, 
                            quantity : updatedQty,
                            total : updatedTotal
                        });
                        updateTotalAndTax(updatedTotalBeforeTax);
                    break;
                    case 3 : 
                        updatedTotal = (float(updatedQty) * float(summary3.rate)).toFixed(2);
                        updatedTotalBeforeTax = (float(summary1.total) + float(summary2.total) + float(updatedTotal)
                            + float(summary4.total) + float(summary5.total));
                        setSummary3({...summary3, 
                            quantity : updatedQty,
                            total : updatedTotal
                        });
                        updateTotalAndTax(updatedTotalBeforeTax);
                    break;
                    case 4 : 
                        updatedTotal = (float(updatedQty) * float(summary4.rate)).toFixed(2);
                        updatedTotalBeforeTax = (float(summary1.total) + float(summary2.total) + float(summary3.total) 
                            + float(updatedTotal) + float(summary5.total));
                        setSummary4({...summary4, 
                            quantity : updatedQty,
                            total : updatedTotal
                        });
                        updateTotalAndTax(updatedTotalBeforeTax);
                    break;
                    case 5 : 
                        updatedTotal = (float(updatedQty) * float(summary5.rate)).toFixed(2);
                        updatedTotalBeforeTax = float(summary1.total) + float(summary2.total) + float(summary3.total) 
                            + float(summary4.total) + float(updatedTotal);
                        setSummary5({...summary5, 
                            quantity : updatedQty,
                            total : updatedTotal
                        });
                        updateTotalAndTax(updatedTotalBeforeTax);
                    break;
                    default : break;
                }
                break;
            case 5 : 
                const updatedRate = validateDecimalInput(value);
                let updatedTotal1;
                let updatedTotalBeforeTax1;
                switch (row) {
                    case 1 : 
                        updatedTotal1 = (float(updatedRate) * float(summary1.quantity)).toFixed(2);
                        updatedTotalBeforeTax1 = (float(updatedTotal1) + float(summary2.total) + float(summary3.total) 
                            + float(summary4.total) + float(summary5.total));
                        setSummary1({...summary1, 
                            rate : updatedRate,
                            total : updatedTotal1
                        }); 
                        updateTotalAndTax(updatedTotalBeforeTax1);
                        break;
                    case 2 : 
                        updatedTotal1 = (float(updatedRate) * float(summary2.quantity)).toFixed(2);
                        updatedTotalBeforeTax1 = (float(summary1.total) + float(updatedTotal1) + float(summary3.total) 
                            + float(summary4.total) + float(summary5.total));
                        setSummary2({...summary2, 
                            rate : updatedRate,
                            total : updatedTotal1
                        });
                        updateTotalAndTax(updatedTotalBeforeTax1);
                        break;
                    case 3 : 
                        updatedTotal1 = (float(updatedRate) * float(summary3.quantity)).toFixed(2);
                        updatedTotalBeforeTax1 = (float(summary1.total) + float(summary2.total) + float(updatedTotal1)
                            + float(summary4.total) + float(summary5.total));
                        setSummary3({...summary3, 
                            rate : updatedRate,
                            total : updatedTotal1
                        });
                        updateTotalAndTax(updatedTotalBeforeTax1);
                        break;
                    case 4 : 
                        updatedTotal1 = (float(updatedRate) * float(summary4.quantity)).toFixed(2);
                        updatedTotalBeforeTax1 = (float(summary1.total) + float(summary2.total) + float(summary3.total) 
                            + float(updatedTotal1) + float(summary5.total));
                        setSummary4({...summary4, 
                            rate : updatedRate,
                            total : updatedTotal1
                        });
                        updateTotalAndTax(updatedTotalBeforeTax1);
                        break;
                    case 5 : 
                        updatedTotal1 = (float(updatedRate) * float(summary5.quantity)).toFixed(2);
                        updatedTotalBeforeTax1 = (float(summary1.total) + float(summary2.total) + float(summary3.total) 
                            + float(summary4.total) + float(updatedTotal1));
                        setSummary5({...summary5, 
                            rate : updatedRate,
                            total : updatedTotal1
                        });
                        updateTotalAndTax(updatedTotalBeforeTax1);
                        break;
                    default : break;
                }
                break;
            default : break;
        };
    }

    const validateDecimalInput = (value) => {
        const regex = /^\d*\.?\d{0,2}$/;
        if (value === '' || regex.test(value)) {
            return value;
        } else {
            return '0.00';
        }
    }

    const float = (value) => {
        return parseFloat(value) || 0;
    };

    const handleTaxChange = (value, index) => {
        const updatedTax = validateDecimalInput(value);
        const updatedTotalAmountBeforeTax = float(totalAmountBeforeTax);
        const taxValue = ( (float(updatedTax) / 100) * updatedTotalAmountBeforeTax ).toFixed(2);
        switch (index) {
            case 1 : 
                setTax({...tax,
                    cgstPercentage : updatedTax,
                    cgstAmount : taxValue
                });
                setTotalAmount((updatedTotalAmountBeforeTax + float(taxValue) + float(tax.sgstAmount) + float(tax.igstAmount)).toFixed(2));
                break;
            case 2 : 
                setTax({...tax,
                    sgstPercentage : updatedTax,
                    sgstAmount : taxValue
                });
                setTotalAmount((updatedTotalAmountBeforeTax + float(taxValue) + float(tax.cgstAmount) + float(tax.igstAmount)).toFixed(2));
                break;
            case 3 : 
                setTax({...tax,
                    igstPercentage : updatedTax,
                    igstAmount : taxValue
                });
                setTotalAmount((updatedTotalAmountBeforeTax + float(taxValue) + float(tax.cgstAmount) + float(tax.sgstAmount)).toFixed(2));
                break;
            default : break;
        };
    }

    const updateTotalAndTax = (updatedTotalBeforeTax) => {
        setTotalAmountBeforeTax(updatedTotalBeforeTax.toFixed(2));
        if(type === 'GST') {
            const updatedCgstAmount = ((float(tax.cgstPercentage) / 100) * float(updatedTotalBeforeTax)).toFixed(2);
            const updatedSgstAmount = ((float(tax.sgstPercentage) / 100) * float(updatedTotalBeforeTax)).toFixed(2);
            const updatedIgstAmount = ((float(tax.igstPercentage) / 100) * float(updatedTotalBeforeTax)).toFixed(2);
            setTax({...tax,
                cgstAmount : updatedCgstAmount,
                sgstAmount : updatedSgstAmount,
                igstAmount : updatedIgstAmount,
            });
            setTotalAmount((float(updatedTotalBeforeTax) + float(updatedCgstAmount) + float(updatedSgstAmount)
                + float(updatedIgstAmount)).toFixed(2));
        } else {
            setTotalAmount(updatedTotalBeforeTax.toFixed(2));
        }
    }

    const onPaymentChange = (value, index) => {
        switch(index) {
            case 1 : setPayment({...payment, paidAmount : validateDecimalInput(value)}); break;
            case 2 : setPayment({...payment, outstandingAmount : validateDecimalInput(value)}); break;
            case 3 : setPayment({...payment, paymentStatus : value}); break;
            case 4 : setPayment({...payment, paymentType : value}); break;
            case 5 : setPayment({...payment, paymentDate : value}); break;
            case 6 : setPayment({...payment, vehicleNumber : value}); break;
            case 7 : setPayment({...payment, referenceNumber : value}); break;
            default: break;
        }
    }

    return (
        <div className="container">
            <div className="row">
                <div className="col-12">              
                    <div className="card"> 
                        <div className="card-header">
                            <span>
                                <b>To : </b>{ selectedCustomer.name }<br />
                                <IoCall /> { selectedCustomer.mobileOne } , { selectedCustomer.mobileTwo } <b>GST : </b>{ selectedCustomer.gstNumber }<br />
                                <IoHome /> { selectedCustomer.addressLine1 }, { selectedCustomer.addressLine2 }, 
                                { selectedCustomer.city }, { selectedCustomer.state } - { selectedCustomer.pinCode }<br />        
                            </span>
                        </div>
                    <div className="card-body">
                        <div className="input-group">
                            {
                                isEdit ? (
                                    <button className="btn btn-dark btn-sm col-2" onClick={ handleButtonClick } disabled={ true }>
                                        <b>{ quote ? 'GST Quotation' : type }</b></button>
                                ) : (
                                    <>
                                        {
                                            (!quote) && 
                                                <>
                                                    <button className="btn btn-dark btn-sm col-1" onClick={ handleButtonClick } disabled={ type !== 'LABOUR' }
                                                    ><b>Labour</b></button>
                                                    <button className="btn btn-dark btn-sm col-1" onClick={ handleButtonClick } disabled={ type !== 'GST' }
                                                    ><b>GST</b></button>  
                                                </>
                                            
                                        }
                                        <input type="text" className="form-control text-end fw-bold" value="GST Quotation only" disabled = { true } />
                                        <div className="input-group-text bg-light">
                                            <input className="form-check-input mt-0" type="checkbox" disabled = { type === 'LABOUR'}
                                            checked = { quote } onChange={ e => { setQuote(e.target.checked) }} />
                                        </div> 
                                    </> 
                                )
                            }
                        </div>

                        <br />
                        
                        <div className="table-responsive table-card">
                            <table className="table text-nowrap mb-0 table-centered table-borderless">
                                <thead className="table-dark">
                                <tr>
                                    <th className="text-center align-middle" style={{width:'3%'}}>#</th>
                                    <th className="text-center align-middle" style={{width:type !== 'GST' ? '47%' : '32%'}}>Description</th>
                                    <th className="text-center align-middle" style={{width:'15%'}}>DC Number</th>
                                    {
                                        type === 'GST' &&
                                        <th className="text-center align-middle" style={{width:'15%'}}>HSN Code</th>
                                    }      
                                    <th className="text-center align-middle" style={{width:'10%'}}>Quantity</th>
                                    <th className="text-center align-middle" style={{width:'15%'}}>Rate</th>
                                    <th className="text-center align-middle" style={{width:'10%'}}>Total</th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td className="text-center align-middle">1</td>
                                    <td>
                                    <textarea className="form-control bg-light" rows="3"
                                    value={summary1.description} onChange={(e) => onSummaryChange(e.target.value, 1, 1)} 
                                    ></textarea>
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text"
                                        value={summary1.dcNumber} onChange={(e) => onSummaryChange(e.target.value, 1, 2)}/>
                                    </td >
                                    {type === 'GST' &&
                                        <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" 
                                        value={summary1.hsnCode} onChange={(e) => onSummaryChange(e.target.value, 1, 3)}
                                        type={type === 'GST' ? "text" : "hidden"}  />
                                    </td>
                                    }
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary1.quantity} onChange={(e) => onSummaryChange(e.target.value, 1, 4)}
                                        />
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary1.rate} onChange={(e) => onSummaryChange(e.target.value, 1, 5)}
                                        />
                                    </td>
                                    <td className="text-end align-middle"><b>&#8377; { summary1.total }</b></td>
                                </tr> 
                                <tr>
                                    <td className="text-center align-middle">2</td>
                                    <td>
                                    <textarea className="form-control bg-light" rows="3"
                                    value={summary2.description} onChange={(e) => onSummaryChange(e.target.value, 2, 1)} 
                                    ></textarea>
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text"
                                        value={summary2.dcNumber} onChange={(e) => onSummaryChange(e.target.value, 2, 2)}/>
                                    </td >
                                    {type === 'GST' &&
                                        <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" 
                                        value={summary2.hsnCode} onChange={(e) => onSummaryChange(e.target.value, 2, 3)}
                                        type={type === 'GST' ? "text" : "hidden"}  />
                                    </td>
                                    }
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary2.quantity} onChange={(e) => onSummaryChange(e.target.value, 2, 4)}
                                        />
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary2.rate} onChange={(e) => onSummaryChange(e.target.value, 2, 5)}
                                        />
                                    </td>
                                    <td className="text-end align-middle"><b>&#8377; { summary2.total }</b></td>
                                </tr> 
                                <tr>
                                    <td className="text-center align-middle">3</td>
                                    <td>
                                    <textarea className="form-control bg-light" rows="3"
                                    value={summary3.description} onChange={(e) => onSummaryChange(e.target.value, 3, 1)} 
                                    ></textarea>
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text"
                                        value={summary3.dcNumber} onChange={(e) => onSummaryChange(e.target.value, 3, 2)}/>
                                    </td >
                                    {type === 'GST' &&
                                        <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" 
                                        value={summary3.hsnCode} onChange={(e) => onSummaryChange(e.target.value, 3, 3)}
                                        type={type === 'GST' ? "text" : "hidden"}  />
                                    </td>
                                    }
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary3.quantity} onChange={(e) => onSummaryChange(e.target.value, 3, 4)}
                                        />
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary3.rate} onChange={(e) => onSummaryChange(e.target.value, 3, 5)}
                                        />
                                    </td>
                                    <td className="text-end align-middle"><b>&#8377; { summary3.total }</b></td>
                                </tr>      
                                <tr>
                                    <td className="text-center align-middle">4</td>
                                    <td>
                                    <textarea className="form-control bg-light" rows="3"
                                    name="summaryList[3].description" value={summary4.description} onChange={(e) => onSummaryChange(e.target.value, 4, 1)} 
                                    ></textarea>
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text"
                                        value={summary4.dcNumber} onChange={(e) => onSummaryChange(e.target.value, 4, 2)}/>
                                    </td >
                                    {type === 'GST' &&
                                        <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" 
                                        value={summary4.hsnCode} onChange={(e) => onSummaryChange(e.target.value, 4, 3)}
                                        type={type === 'GST' ? "text" : "hidden"}  />
                                    </td>
                                    }
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary4.quantity} onChange={(e) => onSummaryChange(e.target.value, 4, 4)}
                                        />
                                    </td>
                                    <td className="text-center align-middle">
                                        <input className="form-control bg-light text-end" type="text" 
                                        value={summary4.rate} onChange={(e) => onSummaryChange(e.target.value, 4, 5)}
                                        />
                                    </td>
                                    <td className="text-end align-middle"><b>&#8377; { summary4.total }</b></td>
                                </tr>
                                {
                                    type === 'GST' &&
                                    <>
                                    <tr>
                                        <td className="text-center align-middle">5</td>
                                        <td>
                                        <textarea className="form-control bg-light" rows="3"
                                        value={summary5.description} onChange={(e) => onSummaryChange(e.target.value, 5, 1)} 
                                        ></textarea>
                                        </td>
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text"
                                            value={summary5.dcNumber} onChange={(e) => onSummaryChange(e.target.value, 5, 2)}/>
                                        </td >
                                        {type === 'GST' &&
                                            <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" 
                                            value={summary5.hsnCode} onChange={(e) => onSummaryChange(e.target.value, 5, 3)}
                                            type={type === 'GST' ? "text" : "hidden"}  />
                                        </td>
                                        }
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text" 
                                            value={summary5.quantity} onChange={(e) => onSummaryChange(e.target.value, 5, 4)}
                                            />
                                        </td>
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text" 
                                            value={summary5.rate} onChange={(e) => onSummaryChange(e.target.value, 5, 5)}
                                            />
                                        </td>
                                        <td className="text-end align-middle"><b>&#8377; { summary5.total }</b></td>
                                    </tr>
                                    <tr className="table-dark">
                                        <th colSpan={type === 'GST' ? '5' : '4'}></th>
                                        <th className="text-center align-middle">Total before Tax</th>
                                        <th className="text-end align-middle">&#8377; { totalAmountBeforeTax } </th>
                                    </tr>
                                    <tr>
                                        <td colSpan={type === 'GST' ? '4' : '3'}></td>
                                        <td className="text-center align-middle">CGST</td>
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text" 
                                            value={ tax.cgstPercentage } onChange={ (e) => handleTaxChange(e.target.value, 1) }
                                            />
                                        </td>
                                        <td className="text-end align-middle"><b>&#8377; { tax.cgstAmount }</b></td>
                                    </tr>
                                    <tr>
                                        <td colSpan={type === 'GST' ? '4' : '3'}></td>
                                        <td className="text-center align-middle">SGST</td>
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text" 
                                            value={ tax.sgstPercentage } onChange={ (e) => handleTaxChange(e.target.value, 2) }
                                            />
                                        </td>
                                        <td className="text-end align-middle"><b>&#8377; { tax.sgstAmount }</b></td>
                                    </tr>
                                    <tr>
                                        <td colSpan={type === 'GST' ? '4' : '3'}></td>
                                        <td className="text-center align-middle">IGST</td>
                                        <td className="text-center align-middle">
                                            <input className="form-control bg-light text-end" type="text" 
                                            value={ tax.igstPercentage } onChange={ (e) => handleTaxChange(e.target.value, 3) }
                                            />
                                        </td>
                                        <td className="text-end align-middle"><b>&#8377; { tax.igstAmount }</b></td>
                                    </tr>
                                    </>
                                }
                                
                                <tr className="table-dark">
                                    <th colSpan={type === 'GST' ? '5' : '4'}></th>
                                    <th className="text-center align-middle">Total</th>
                                    <th className="text-end align-middle">&#8377; { totalAmount }</th>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                            
                    </div>

                    {
                        !quote &&
                        <div className="card-footer bg-white">
                            <div className="row">
                                <div className="col-6">
                                    <table className="table text-nowrap mb-0 table-centered table-borderless">
                                        <thead className="table-dark">
                                            <tr>
                                                <th className="text-center align-middle" colSpan="2">
                                                    Payment Details
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>Paid Amount</td>
                                                <td>
                                                    <input className="form-control bg-light text-end" type="text"
                                                        value={ payment.paidAmount } onChange={ (e) => onPaymentChange(e.target.value, 1) }
                                                        />
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>Outstanding Amount</td>
                                                <td>
                                                    <input className="form-control bg-light text-end" type="text"
                                                        value={ payment.outstandingAmount } onChange={ (e) => onPaymentChange(e.target.value, 2) }
                                                        />
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>Payment Status</td>
                                                <td>   
                                                    <select className="form-select bg-light" value={ payment.paymentStatus }
                                                        onChange={ (e) => onPaymentChange(e.target.value, 3) }>
                                                        <option value="NOT_PAID">Not Paid</option>
                                                        <option value="PARTIALLY_PAID">Partially Paid</option>
                                                        <option value="PAID">Paid</option>
                                                    </select>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>Payment Type</td>
                                                <td>
                                                    <select className="form-select bg-light" value={ payment.paymentType }
                                                        onChange={ (e) => onPaymentChange(e.target.value, 4) }>
                                                        <option value="NA">Select...</option>
                                                        <option value="CASH">Cash</option>
                                                        <option value="CHEQUE">Cheque</option>
                                                        <option value="NEFT">NEFT</option>
                                                        <option value="RTGS">RTGS</option>
                                                        <option value="UPI">UPI</option>
                                                    </select>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>Payment Date</td>
                                                <td>
                                                    <input className="form-control bg-light" type="date" value={ payment.paymentDate } 
                                                        onChange={ (e) => onPaymentChange(e.target.value, 5) }/>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div className="col-6">
                                    <table className="table text-nowrap mb-0 table-centered table-borderless">
                                        <thead className="table-dark">
                                            <tr>
                                                <th className="text-center align-middle" colSpan="2">
                                                    Other Details
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>Vehicle Number</td>
                                                <td>
                                                    <input className="form-control bg-light text-end" value={ payment.vehicleNumber } 
                                                        onChange={ (e) => onPaymentChange(e.target.value, 6) }/>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>Reference Number</td>
                                                <td>
                                                    <input className="form-control bg-light text-end" value={ payment.referenceNumber }
                                                        onChange={ (e) => onPaymentChange(e.target.value, 7) }/>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    }   

                    </div>
                </div>
            </div>
        </div>
    )
}

export default InvoiceFormStep2