import React, { createContext, useState, useContext, useEffect } from 'react';

import { getAllCustomers } from './CustomerService'

// Create a context for customers
const CustomerContext = createContext();

// Custom hook to use the customer context
export const useCustomers = () => {
  return useContext(CustomerContext);
};

export const CustomerProvider = ({ children }) => {

    const [customers, setCustomers] = useState([]);
    const [customerLoading, setCustomerLoading] = useState(false);
    
    const getCustomersList = () => {
      setCustomerLoading(true);
      // setTimeout(() => {
        getAllCustomers()
        .then((customers) => {
          console.log("CustomerContext.getCustomerList()::Size::" + customers.length);
          setCustomers(customers);
        })
        .catch((err) => {
          setCustomers([]);
          console.log("CustomerContext.getCustomerList()::Error::" + err);
        })
        .finally(() => {
          setCustomerLoading(false);
        });
      // }, 10000);
    }

    useEffect(() => { getCustomersList(); }, []); 

    return (
        <CustomerContext.Provider value={{ customers, getCustomersList, customerLoading }}>
          {children}
        </CustomerContext.Provider>
      );
};