import React, { useState } from 'react'
import { IoReload, IoSearchOutline } from 'react-icons/io5'
import { Tooltip } from 'react-tooltip';
import { useCustomers } from '../../../service/CustomerContext';
import DeletePopup from '../../popup/DeletePopup';

const SearchBar = ({ onClickSearch, filter, setFilter, onClickResetSearch, setCustomerName, setValidFilterForExport }) => {

    const { customers } = useCustomers();
    
    const [type, setType] = useState(filter.type);
    const [status, setStatus] = useState(filter.paymentStatus);
    const [customerId, setCustomerId] = useState(filter.customerId);
    const [fromDate, setFromDate] = useState(filter.fromDate);
    const [toDate, setToDate] = useState(filter.toDate);

    const [errMessage, setErrMessage] = useState('');
    const [reset, setReset] = useState(false);

    const setCustomerDetails = (id, name) => {
        setCustomerId(id);
        setCustomerName((id === '0' ? 'All Customers' : name));
    }

    const onClickFilter = () => { 
        const filterObj = {
            type : type,
            paymentStatus : status,
            customerId : customerId,
            fromDate : (fromDate === null || fromDate === '') ? '' : new Date(fromDate).toISOString(),
            toDate : (toDate === null || toDate === '') ? '' : new Date(toDate).toISOString()
        };
        let flag = true;
        if(filterObj.fromDate === '1970-01-01T00:00:00.000Z' || filterObj.fromDate === '' 
            || filterObj.toDate === '1970-01-01T00:00:00.000Z' || filterObj.toDate === '') {
            setErrMessage("From and To date required");
            flag = false;
        } else if (new Date(fromDate) > new Date(toDate)) {
            setErrMessage("From Date must be earlier than To Date");
            flag = false;
        }
        if(flag) {
            if(filterObj.customerId != '0' && filterObj.customerId !== '') {
                setValidFilterForExport(true);
            } else {
                setValidFilterForExport(false);
            }
            setFilter(filterObj);
            onClickSearch(filterObj);
        } else {
            setValidFilterForExport(false);
        }
    }

    const onClickResetConfirm = () => {
        setType('NA');
        setStatus('NA');
        setCustomerId(0);
        setCustomerName('');
        setFromDate('');
        setToDate('');
        setErrMessage('');
        setReset(false);
        setValidFilterForExport(false);
        onClickResetSearch();
    }

    const getDateInYYYYMMDDFormat = (dateObj) => {
        if(dateObj === null || dateObj === '') return '';
        const date = new Date(dateObj);

        const yyyy = date.getFullYear();
        const mm = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-indexed
        const dd = String(date.getDate()).padStart(2, '0');
        const formattedDate = `${yyyy}-${mm}-${dd}`;

        return formattedDate; // Output: 2025-06-13
    }

    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-12"> 
                    <div className="card" style={{marginBottom : '5px'}}>
                        <div className="card-header">
                            <div className="row w-100">
                                <div className="col-2">
                                    <select className="form-select" onChange={(e) => setType(e.target.value)}
                                        value={ type }>
                                        <option value="NA">Type</option>
                                        <option value="GST">GST</option>
                                        <option value="LABOUR">Labour</option>
                                    </select>
                                </div>
                                <div className="col-2">
                                    <select className="form-select" onChange={(e) => setStatus(e.target.value)}
                                        value= { status }>
                                        <option value="NA">Status</option>
                                        <option value="NOT_PAID">Not Paid</option>
                                        <option value="PARTIALLY_PAID">Partially Paid</option>
                                        <option value="PAID">Paid</option>
                                    </select>
                                </div>
                                <div className="col-2">
                                    <select className="form-select" 
                                    onChange={(e) => setCustomerDetails(e.target.value, e.target.options[e.target.selectedIndex].text)}
                                        value={ customerId }>
                                        <option value="0">Customer</option>
                                        { customers.map((i) => (
                                            <option value={i.id} key={i.id}>{i.name}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="col-2">
                                    <div className="input-group">
                                        <span className="input-group-text">From</span>
                                        <input type="date" className="form-control" onChange={(e) => setFromDate(e.target.value)}
                                        value={ getDateInYYYYMMDDFormat(fromDate) }/>
                                    </div>
                                </div>
                                <div className="col-2">
                                    <div className="input-group">
                                        <span className="input-group-text">To</span>
                                        <input type="date" className="form-control" onChange={(e) => setToDate(e.target.value)}
                                        value={ getDateInYYYYMMDDFormat(toDate) }/>
                                    </div>
                                </div>
                                <div className="col-1"></div>
                                <div className="col-1 d-flex justify-content-center align-items-center">

                                    <button className="btn btn-primary btn-sm" type="button" data-tooltip-id={`pt-search`}
                                    onClick={ onClickFilter }><IoSearchOutline /></button>
                                    <Tooltip id={`pt-search`} className="tooltip-content" style={{zIndex:'2000'}}>Search</Tooltip>
                                    
                                    <span>&nbsp;</span>

                                    <button className="btn btn-danger btn-sm" type="button" data-tooltip-id={`pt-reset`}
                                    onClick={ e => { setReset(true) } }><IoReload /></button>
                                    <Tooltip id={`pt-reset`} className="tooltip-content" style={{zIndex:'2000'}}>Reset Filter</Tooltip>
                                    
                                </div>
                            </div>
                            {
                                errMessage !== '' &&
                                <div className="row w-100 text-center align-middle" style={{paddingTop : '5px'}}>
                                    <div className="text text-danger">{ errMessage }</div>
                                </div>
                            }

                            { reset &&
                                <DeletePopup 
                                message = {'Are you sure you want to Reset Filter?'}
                                yes = {'Yes'}
                                no = {'No'}
                                onClickDeleteConfirm = { onClickResetConfirm }
                                onClickDeleteCancel = { e => { setReset(false) } }
                                />
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SearchBar