import React from 'react'

import { IoTrash, IoPeopleOutline, IoPencil, IoDownloadOutline, IoPricetagOutline, IoEllipsisVertical } from 'react-icons/io5'
import { Tooltip } from 'react-tooltip';
import "./InvoiceList.css"
import { Dropdown } from 'react-bootstrap';

const InvoiceList = ({ invoices, customers, loading, onClickCreate, onClickEdit, onClickDelete, onClickUpdate, headingMessage, onClickExport, validFilterForExport }) => {

    const getCustomerById = (customer) => {
        if (!customer) {
            return <div>Customer not found</div>; // or return null or a placeholder
        }
        return ( 
            <table>
                <tbody>
                    <tr><td>Name</td><td>:</td><td>{ customer.name }</td></tr>
                    <tr><td>Mobile</td><td>:</td><td>{ customer.mobileOne }, { customer.mobileTwo }</td></tr>
                    <tr><td>Address</td><td>:</td><td>{ customer.addressLine1}, {customer.addressLine2}, {customer.city}, {customer.state} - {customer.pinCode }</td></tr>
                    <tr><td>GST</td><td>:</td><td>{ customer.gstNumber }</td></tr>
                </tbody> 
            </table>
        );    
    }

    const getPaymentStatusStyle = (status) => {
        switch (status) {
            case "PAID" || "GST":
                return "bg-success text-white";
            case "PARTIALLY_PAID":
                return "bg-warning text-dark";
            case "NOT_PAID" || "LABOUR":
                return "bg-danger text-white";
            default:
                return "";
        }
    }

    const getPaymentStatusText =  (status) => {
        switch (status) {
            case "PAID":
                return "Paid";
            case "PARTIALLY_PAID":
                return "Partially Paid";
            case "NOT_PAID":
                return "Not Paid";
            default:
                return "";
        }
    }
    
    return (
    <>  
    { !loading &&
        <div className="container-fluid">
            <div className="row">
                <div className="col-12">              
                    <div className="card">
                        <div className="card-header">
                            <div className="row">
                                <div className="col-9">{ headingMessage }</div>
                                <div className="col-3 text-end">
                                    <button className="btn btn-secondary btn-sm fw-bold" onClick={ () => onClickExport()}
                                        disabled={ !validFilterForExport }><IoDownloadOutline/> Export PDF</button>
                                    <span>&nbsp;</span>
                                    <button className="btn btn-primary btn-sm fw-bold" onClick={ () => onClickCreate()}>Create Invoice</button>
                                </div>
                            </div>
                        </div>

                        <div className="card-body">
                            {
                                (invoices === null || invoices.length < 1)
                                ? 
                                (
                                    <div className="d-flex justify-content-center align-items-center" style={{ minHeight: '70vh' }}>
                                        <span className="text-center align-middle">
                                            No Invoices found with the applied filter<br/>(or)<br/>No Filter applied 
                                        </span>
                                    </div>
                                ) : 
                                (
                                    <table className="table text-nowrap mb-0 table-centered table-responsive table-card">
                                    <thead className="table-light">
                                    <tr>
                                        <th className="text-center align-middle" >#</th>
                                        <th className="text-center align-middle" >Customer</th>
                                        <th className="text-center align-middle" >Type</th>
                                        <th className="text-center align-middle" >Total Amount</th>
                                        <th className="text-center align-middle" >Paid Amount</th>
                                        <th className="text-center align-middle" >TDS</th>
                                        <th className="text-center align-middle" >Outstanding</th>
                                        <th className="text-center align-middle" >Status</th> 
                                        <th className="text-center align-middle" >Action</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <>
                                    {(() => {
                                        let totalToBeDisplayed = 0;
                                        let paidToBeDisplayed = 0;
                                        let tdsToBeDisplayed = 0;
                                        let outstandingToBeDisplayed = 0;
                                        return (
                                        <>
                                            {invoices.map((i) => {
                                                const customer = customers.find((j) => j.id === i.customerId );
                                                totalToBeDisplayed += (parseFloat(i.totalAmount) || 0);
                                                paidToBeDisplayed += (parseFloat(i.paidAmount) || 0);
                                                tdsToBeDisplayed += (parseFloat(i.tdsAmount) || 0);
                                                outstandingToBeDisplayed += (parseFloat(i.outstandingAmount) || 0);
    
                                                return (         
                                                <tr key={i.id}>
                                                    <td className="text-center align-middle" >
                                                        <span data-tooltip-id={`invoice-audit-tooltip-${i.id}`}>{ i.id }</span>
                                                        <Tooltip id={`invoice-audit-tooltip-${i.id}`} className="tooltip-content" style={{zIndex:'2000'}}>
                                                            <table>
                                                                <tbody>
                                                                    <tr><td>Created By</td><td>:</td><td>{ i.createdBy }</td></tr>
                                                                    <tr><td>Created Date</td><td>:</td><td>{ i.createdDate }</td></tr>
                                                                    <tr><td>Modified By</td><td>:</td><td>{ i.modifiedBy }</td></tr>
                                                                    <tr><td>Modified Date</td><td>:</td><td>{ i.modifiedDate }</td></tr>
                                                                </tbody>
                                                            </table>
                                                        </Tooltip>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <button className="btn btn-light btn-sm" data-tooltip-id={`invoice-customer-tooltip-${i.id}`}>
                                                            <IoPeopleOutline />
                                                        </button>
                                                        <Tooltip id={`invoice-customer-tooltip-${i.id}`} className="tooltip-content" style={{zIndex:'2000'}}>
                                                            { getCustomerById(customer) }
                                                        </Tooltip>   
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span className={i.quote ? 'badge bg-primary' : (i.type === 'GST' ? 'badge bg-success' : 'badge bg-danger') }>
                                                            {i.quote ? 'GST - Quote' : i.type}</span>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span>&#8377; { i.totalAmount }</span>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span>&#8377; { i.paidAmount }</span>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span>&#8377; { i.tdsAmount }</span>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span>&#8377; { i.outstandingAmount }</span>
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <span className={`badge ${getPaymentStatusStyle(i.paymentStatus)}`}>{ getPaymentStatusText(i.paymentStatus)}</span>    
                                                    </td>
                                                    <td className="text-center align-middle">
                                                        <Dropdown>
                                                            <Dropdown.Toggle variant="light" id="dropdown-basic">
                                                                <IoEllipsisVertical /> 
                                                            </Dropdown.Toggle>
                                                            <Dropdown.Menu>
                                                                <Dropdown.Item className="py-1 px-1">
                                                                    <button className="btn" onClick={() => onClickUpdate(i)}><IoPricetagOutline /> Update Payment</button>
                                                                </Dropdown.Item>
                                                                <Dropdown.Item className="py-1 px-1">
                                                                    <button className="btn" onClick={() => onClickEdit(i, customer)}><IoPencil /> Edit Invoice</button>   
                                                                </Dropdown.Item>
                                                                <Dropdown.Item className="py-1 px-1">
                                                                    <button className="btn" onClick={() => onClickDelete(i.id)}><IoTrash/> Delete Invoice</button> 
                                                                </Dropdown.Item>
                                                            </Dropdown.Menu>
                                                        </Dropdown>
                                                    </td>
                                                </tr>
                                                )
                                            })} 
                                            <tr>
                                                <th className="text-center align-middle" ></th>
                                                <th className="text-center align-middle" ></th>
                                                <th className="text-center align-middle" >Total</th>
                                                <th className="text-center align-middle" >&#8377; { totalToBeDisplayed.toFixed(2) }</th>
                                                <th className="text-center align-middle" >&#8377; { paidToBeDisplayed.toFixed(2) }</th>
                                                <th className="text-center align-middle" >&#8377; { tdsToBeDisplayed.toFixed(2) }</th>
                                                <th className="text-center align-middle" >&#8377; { outstandingToBeDisplayed.toFixed(2) }</th>
                                                <th className="text-center align-middle" ></th> 
                                                <th className="text-center align-middle" ></th>
                                            </tr>
                                        </>
                                        );
                                    })()}
                                    </>   
                                    </tbody>
                                    </table>
                                )
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
        
    </>
    )
}

export default InvoiceList