import React, { useState }  from 'react'
import "./CustomerForm.css"
import Spinner from "../../spinner/Spinner"

import { Modal, Button } from 'react-bootstrap'; 
import { IoCall, IoHome, IoMailOpenSharp } from 'react-icons/io5'
import { createCustomer, updateCustomer } from '../../../service/CustomerService';

const CustomerForm = ({ submitForm, closeForm, isEdit, editCustomer }) => {

  var customerobj = (isEdit) ? editCustomer : {
    id: 0,
    name: '',
    gstNumber: '',
    mobileOne: '',
    mobileTwo: '',
    email : '',
    addressLine1 : '',
    addressLine2 : '',
    city : '',
    state : '',
    pinCode : ''
  };

  const [customer, setCustomer] = useState(customerobj);
  const [isLoading, setIsLoading] = useState(false);
  const [errMessage, setErrMessage] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCustomer({...customer, [name]: value,});
  };

  const onClickSubmit = (e) => {
    e.preventDefault();
    console.log("CustomerForm.onClickSubmit()::Customer::" + JSON.stringify(customer));
    
    if(  customer.name !== null && customer.name !== ''
      && customer.mobileOne !== null && customer.mobileOne !== ''
      && customer.addressLine1 !== null && customer.addressLine1 !== ''
      && customer.city !== null && customer.city !== ''
      && customer.state !== null && customer.state !== ''
      && customer.pinCode !== null && customer.pinCode !== ''
    ) {
      setErrMessage('');
      setIsLoading(true);
      if(isEdit) {
        updateCustomer(customer)
        .then((data) => {
          console.log(data);
        }).catch((err) => {
          console.log(err);
        }).finally(() => {
          setIsLoading(false);
          submitForm();
        });
      } else {
        createCustomer(customer)
        .then((data) => {
          console.log(data);
        }).catch((err) => {
          console.log(err);
        }).finally(() => {
          setIsLoading(false);
          submitForm();
        });
      }
    } else {
      setErrMessage("Please enter all the required fields");
    }  
  }

  const validate = (value) => {
    return (value === null || value === '') ? 'is-invalid' : ''; 
  }

  return (
    <>
      { isLoading && <Spinner/>  } 

      {!isLoading &&
      <Modal show='true' onHide={ () => closeForm() } size='lg' centered>
        <Modal.Header closeButton>
          <Modal.Title>{ isEdit ? 'Edit' : 'Create' } Customer</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form>
            <div className="container">
              <div className="row mb-3">
                <div className="col-12">
                  <div className="input-group">
                    <span className="input-group-text">@</span>
                    <input type="text" className={`form-control ${validate(customer.name)}`}  placeholder="Customer Name *" 
                    name="name" value={customer.name} onChange={handleChange} required/>
                  </div>  
                </div>
              </div>
              <div className="row mb-3">
                <div className="col-6">
                  <div className="input-group">
                    <span className="input-group-text"><IoCall /></span>
                    <input type="text" className={`form-control ${validate(customer.mobileOne)}`} placeholder="Mobile Number 1 *"
                    name="mobileOne" value={customer.mobileOne} onChange={handleChange} required/>
                  </div>
                </div>
                <div className="col-6">
                  <div className="input-group">
                    <span className="input-group-text"><IoCall /></span>
                    <input type="text" className="form-control" placeholder="Mobile Number 2" 
                    name="mobileTwo" value={customer.mobileTwo} onChange={handleChange} />
                  </div>
                </div>  
              </div>
              <div className="row mb-3">
                <div className="col-12">
                  <div className="input-group">
                    <span className="input-group-text"><IoHome /></span>
                    <input type="text" className={`form-control ${validate(customer.addressLine1)}`} placeholder="Address line 1 *" 
                    name="addressLine1" value={customer.addressLine1} onChange={handleChange} required/>
                  </div>
                </div>
              </div>
              <div className="row mb-3">
                <div className="col-12">
                  <div className="input-group">
                    <span className="input-group-text"><IoHome /></span>
                    <input type="text" className="form-control" placeholder="Address line 2"
                    name="addressLine2" value={customer.addressLine2} onChange={handleChange}/>
                  </div>
                </div>
              </div>
              <div className="row mb-3">
                <div className="col-4">
                  <input className={`form-control ${validate(customer.city)}`} type="text" placeholder="City *" 
                  name="city" value={customer.city} onChange={handleChange} required/>
                </div>
                <div className="col-4">
                  <input className={`form-control ${validate(customer.state)}`} type="text" placeholder="State *" 
                  name="state" value={customer.state} onChange={handleChange} required/>
                </div>
                <div className="col-4">
                  <input className={`form-control ${validate(customer.pinCode)}`} type="text" placeholder="Pincode *" 
                  name="pinCode" value={customer.pinCode} onChange={handleChange} required/>
                </div>
              </div>
              <div className="row mb-3">
                <div className="col-6">
                  <div className="input-group">
                    <input type="text" className="form-control" placeholder="GST Number" 
                    name="gstNumber" value={customer.gstNumber} onChange={handleChange} />
                  </div>
                </div>
                <div className="col-6">
                  <div className="input-group">
                    <span className="input-group-text"><IoMailOpenSharp /></span>
                    <input type="text" className="form-control" placeholder="Email"
                    name="email" value={customer.email} onChange={handleChange}/>
                  </div>
                </div>  
              </div>
              <div className="row mb-3 text-center align-middle">
                <div className="text text-danger">{ errMessage }</div>
              </div>
            </div>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={ () => closeForm() }>Cancel</Button>
          <Button variant="primary" onClick={ onClickSubmit }>Save</Button>
        </Modal.Footer>
      </Modal>
      }
    </>
  )
}

export default CustomerForm