import { INVOICE_URL } from "./Api";

export const getInvoicesByFilter = async (filter) => {
    try {
        const response = await fetch(INVOICE_URL + "filter-invoices", {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            },
            body: JSON.stringify(filter),
        }); 
        if(response.ok) {
            const body = await response.json();
            const invoiceList = body.data.sort((a, b) => a.id - b.id);    
            return invoiceList;
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::InvoiceService.getInvoicesByFilter()");
        throw err;
    } 
}

export const createLabourInvoice = async (customer, invoice) => {
    try {
        const arr = [customer, invoice];
        const response = await fetch(INVOICE_URL + "create-labour-invoice", {
            method: 'POST',
            body: JSON.stringify(arr),
        }); 
        if(response.ok) {
            const json = await response.json();
            const blob = base64ToBlob(json.fileData, 'application/pdf');
            downloadPdf(blob, json.fileName);
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::InvoiceService.createLabourInvoice()");
        throw err;
    }
}

