export const downloadPdf = async (blob, fileName) => {
    const url = URL.createObjectURL(blob); // Create a blob URL
    const a = document.createElement('a'); // Create a temporary anchor tag to trigger download
    a.href = url;
    a.download = fileName; // FileName : {<invoice_id>.pdf}
    document.body.appendChild(a);
    a.click(); // triggers the download
    document.body.removeChild(a);
    const printWindow = window.open(url, '_blank');  // Open the blob URL in a new tab for print preview
    if (printWindow) {
        printWindow.onload = () => {
        printWindow.focus();
        printWindow.print();
        };
    }
    setTimeout(() => URL.revokeObjectURL(url), 5000); // Revoke the blob URL after some time to free memory
}

function base64ToBlob(base64, contentType = '', sliceSize = 512) {
    const byteCharacters = atob(base64); // decode base64
    const byteArrays = [];
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        const slice = byteCharacters.slice(offset, offset + sliceSize);
        const byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
            byteNumbers[i] = slice.charCodeAt(i);
        }
        const byteArray = new Uint8Array(byteNumbers);
        byteArrays.push(byteArray);
    }
    return new Blob(byteArrays, { type: contentType });
}

export const updatePayment = async (payment, id, loggedInUser) => {
    try {
        const response = await fetch(INVOICE_URL + "update-payment?id=" + id, {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            },
            body: JSON.stringify([payment, loggedInUser]),   
        }); 
        if(response.ok) {
            const body = await response.json();
            return body;
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::InvoiceService.updatePayment().ID::" + id);
        throw err;
    } 
}

export const downloadExport = async (filter, customer) => {
    try {
        const arr = [customer, filter];
        const response = await fetch(INVOICE_URL + "export", {
            method: 'POST',
            body: JSON.stringify(arr),
        }); 
        if(response.status === 200) {
            const json = await response.json();
            const blob = base64ToBlob(json.fileData, 'application/pdf');
            downloadPdf(blob, json.fileName);
        } else {
            const errorBody = await response.json();
            throw errorBody;
        }
    } catch(err) {
        console.log("Error::InvoiceService.downloadExport()");
        throw err;
    }
}

