import React, { useState } from 'react'

import { IoTrash, IoPencil, IoHome } from 'react-icons/io5'
import { Tooltip } from 'react-tooltip';


const CustomerList = ({ customers, onEdit, onDelete, onClickCreate }) => {

  const [search, setSearch] = useState('');

  const filteredCustomerList = search ? customers.filter((item) =>
    item.name.toLowerCase().includes(search.toLowerCase())
    || item.mobileOne.toLowerCase().includes(search.toLowerCase())
    || (item.mobileTwo !== null && item.mobileTwo.toLowerCase().includes(search.toLowerCase()))
  ) : customers;

  return (
    <section>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">              
            <div className="card">

              <div className="card-header">
                <div className="row w-100">
                  <div className="col-3 d-flex align-items-center">
                    <span className="align-middle">Showing {filteredCustomerList.length} Customers</span>
                  </div>
                  <div className="col-6 d-flex align-items-center">
                    <input type="text" className="form-control" placeholder="Search" 
                      value={ search }
                      onChange={ e => setSearch(e.target.value) }/>
                  </div>
                  <div className="col-3 d-flex align-items-center justify-content-end">
                    <button className="btn btn-dark btn-sm" onClick={ () => onClickCreate() }>Create Customer</button>
                  </div>
                </div>
              </div>

              <div className="card-body">
                { (filteredCustomerList.length > 0) ? 
                <div className="table-responsive table-card">
                  <table className="table text-nowrap mb-0 table-centered">
                    <thead className="table-light">
                      <tr>
                        <th className="text-center align-middle" >#</th>
                        <th className="text-center align-middle" >Name</th>
                        <th className="text-center align-middle" >Mobile</th> 
                        <th className="text-center align-middle" >Email</th>
                        <th className="text-center align-middle" >GST</th>
                        <th className="text-center align-middle" >Address</th>
                        <th className="text-center align-middle" >Action</th>
                      </tr>
                    </thead>
                    <tbody>
                      { filteredCustomerList.map((i) => (
                        <tr key={i.id}>
                          <td className="align-middle" data-tooltip-id={`cust-audit-${i.id}`}>
                            <span >{ i.id }</span>
                            <Tooltip id={`cust-audit-${i.id}`} className="tooltip-content" style={{zIndex:'2000'}}>
                              <table>
                                <tbody>
                                  <tr><td>Created By</td><td>:</td><td>{ i.createdBy }</td></tr>
                                  <tr><td>Created Date</td><td>:</td><td>{ i.createdDate }</td></tr>
                                  <tr><td>Modified By</td><td>:</td><td>{ i.modifiedBy }</td></tr>
                                  <tr><td>Modified Date</td><td>:</td><td>{ i.modifiedDate }</td></tr>
                                </tbody>
                              </table>
                            </Tooltip>
                          </td>
                          <td className="text-center align-middle">
                            <span>{ i.name }</span>
                          </td>
                          <td className="text-center align-middle">
                            <span>{ i.mobileOne } { (i.mobileTwo === '') ? '' : (',' + i.mobileTwo) }</span>
                          </td>
                          <td className="text-center align-middle">
                            <span>{ i.email }</span>
                          </td>
                          <td className="text-center align-middle">
                            <span>{ i.gstNumber }</span>
                          </td>
                          <td className="text-center align-middle">
                          <button className="btn btn-light btn-sm" data-tooltip-id={`cust-other-${i.id}`}>
                            <IoHome /></button>
                            <Tooltip id={`cust-other-${i.id}`} className="tooltip-content" style={{zIndex:'2000'}}>
                              <span>
                              { i.addressLine1 } { (i.addressLine2 === '') ? '' : (',' + i.addressLine2) }, { i.city }, { i.state } - { i.pinCode }
                              </span>  
                            </Tooltip>
                            <span>&nbsp;</span>
                          </td>
                          <td className="text-center align-middle">
                            <button className="btn btn-primary btn-sm" onClick={ () => onEdit(i) }><IoPencil /></button>
                            <span>&nbsp;</span>
                            <button className="btn btn-danger btn-sm" onClick={ () => onDelete(i.id) }><IoTrash /></button> 
                          </td>
                      </tr>         
                      ))}      
                    </tbody>
                  </table>
                </div>
                : <p className="text-center align-middle" >No Customers found</p> 
                }
              </div>
              
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default CustomerList